* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu';
    position: relative;
    font-weight: 600;
}

.activity-container {
    margin: 20px auto;
    background: linear-gradient(to right, #313866, #4477CE, #27005D);
    padding: 20px;
    color: #fff;
    min-width: 60%;
    max-width: 80%;
    border-radius: 10px;
    font-size: 1.2rem;
    overflow: hidden;
    position: relative;
}

.activity-container::before {
    content: "";
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #313866, #4477CE, #27005D);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    animation: showActivity 2s ease-in 1s forwards;
}

@keyframes showActivity {
    0% {
        width: 100%;
    }

    100% {
        width: 0%;
    }
}

.activity-type {
    margin-block: 20px;
}

.activity-type span {
    text-shadow: 1px 1px 3px black;
    text-transform: capitalize;
}

.activity-text span {
    text-shadow: 1px 1px 3px black;
    text-transform: capitalize;
}

h2 {
    text-align: center;
    margin-block: 20px;
}

h2+p {
    text-align: center;
    font-size: 1.1rem;
    font-weight: 600;
}

button {
    padding: 6px 10px;
    border: none;
    background-color: #4477CE;
    color: white;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
    transition: .7s;
    display: block;
    margin: auto;
}

button:hover {
    box-shadow: none;
}

.reaction {
    width: 90px;
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #05BFDB;
    padding: 1px 10px;
    border-radius: 10px;

}

.reaction span i {
    cursor: pointer;
    transition: 1s;
    font-size: 1rem;
    filter: contrast(120%);

}

.thumbsClick {
    color: blue;
    text-shadow: 1px 1px 1px black;
}